//Calculates scores based on prior XG figures, weighted by odds
export async function calculateScore(match, index, divider, id) {
  let homeRaw;
  let awayRaw;

  let teams;
  let calculate = true;

  let gameTotalWeighting;

  switch (true) {
    case divider === 5:
      gameTotalWeighting = 5;
      break;
    case divider === 6:
      gameTotalWeighting = 5;
      break;
    case divider === 10:
      gameTotalWeighting = 5;
      break;
    default:
      break;
  }

  if (
    allForm.find(
      (game) =>
        game.home.teamName === match.homeTeam &&
        game.away.teamName === match.awayTeam
    )
  ) {
    teams = [
      allForm.find((game) => game.home.teamName === match.homeTeam).home,
      allForm.find((game) => game.home.teamName === match.homeTeam).away,
    ];
  } else {
    calculate = false;
  }

  if (calculate) {
    for (let i = 0; i < teams.length; i++) {
      teams[i][index].lastGame = teams[i][index].LastFiveForm[4];
      teams[i][index].previousToLastGame = teams[i][index].LastFiveForm[3];

      teams[i][index].generalOffensiveRating =
        (teams[i][index].ScoredOverall / divider + teams[i][index].XG) / 2;
      teams[i][index].homeOffensiveRating =
        (teams[i][index].ScoredHome / teams[i][index].PlayedHome +
          teams[i][index].XGHome) /
        2;
      teams[i][index].awayOffensiveRating =
        (teams[i][index].ScoredAway / teams[i][index].PlayedAway +
          teams[i][index].XGAway) /
        2;

      teams[i][index].generalDefensiveRating =
        (teams[i][index].ConcededOverall / divider +
          teams[i][index].XGAgainstAvg) /
        2;

      teams[i][index].homeDefensiveRating =
        (teams[i][index].ConcededHome / teams[i][index].PlayedHome +
          teams[i][index].XGAgainstHome) /
        2;
      teams[i][index].awayDefensiveRating =
        (teams[i][index].ConcededAway / teams[i][index].PlayedAway +
          teams[i][index].XGAgainstAway) /
        2;

      if (parseFloat(teams[i][2].ScoredOverall) > 0) {
        teams[i][index].finishingScore = parseFloat(
          (teams[i][2].XG / (teams[i][2].ScoredOverall / 10)).toFixed(2) - 1
        );
      } else {
        teams[i][index].finishingScore = 0.0;
      }

      if (teams[i][2].ConcededOverall > 0) {
        teams[i][index].goalieRating = parseFloat(
          (
            teams[i][2].XGAgainstAvg /
            (teams[i][2].ConcededOverall / 10)
          ).toFixed(2) - 1
        );
      } else {
        teams[i][index].goalieRating = 0.0;
      }

      teams[i][index].defenceScore = parseInt(
        teams[i][index].CleanSheetPercentage
      );

      let defenceScore;
      defenceScore = teams[i][index].defenceScore;

      teams[i][index].scoredAverage = teams[i][index].ScoredOverall / divider;
      teams[i][index].concededAverage =
        teams[i][index].ConcededOverall / divider;

      switch (true) {
        case defenceScore === 0:
          teams[i][index].defenceRating = 1.5;
          break;
        case defenceScore > 0 && defenceScore < 20:
          teams[i][index].defenceRating = 1.35;
          break;
        case defenceScore >= 20 && defenceScore < 30:
          teams[i][index].defenceRating = 1.2;
          break;
        case defenceScore >= 30 && defenceScore < 40:
          teams[i][index].defenceRating = 1.1;
          break;
        case defenceScore >= 40 && defenceScore < 50:
          teams[i][index].defenceRating = 1;
          break;
        case defenceScore >= 50 && defenceScore < 60:
          teams[i][index].defenceRating = 0.9;
          break;
        case defenceScore >= 60 && defenceScore < 70:
          teams[i][index].defenceRating = 0.7;
          break;
        case defenceScore >= 70 && defenceScore < 80:
          teams[i][index].defenceRating = 0.6;
          break;
        case defenceScore >= 80:
          teams[i][index].defenceRating = 0.4;
          break;
        default:
          break;
      }

      teams[0][index].homeAttackAdvantageWeighted = parseFloat(
        teams[0][index].homeAttackAdvantage / 2
      );
      teams[0][index].homeDefenceAdvantageWeighted = parseFloat(
        teams[0][index].homeDefenceAdvantage / 2
      );

      teams[i][index].finalFinishingScore = parseFloat(
        teams[i][index].finishingScore / gameTotalWeighting + 1
      );

      teams[i][index].XGConcededAdjustment = parseFloat(
        teams[i][index].goalieRating / gameTotalWeighting + 1
      );

      teams[i][index].forecastedXG = parseFloat(teams[i][index].scoredAverage);

      teams[i][index].forecastedXGConceded = parseFloat(
        teams[i][index].concededAverage
      );

      teams[0][index].goalsBasedOnAverages = Math.round(
        parseFloat(
          (teams[0][index].forecastedXG +
            teams[1][index].forecastedXGConceded) /
            2
        )
      );

      teams[1][index].goalsBasedOnAverages = Math.round(
        parseFloat(
          (teams[1][index].forecastedXG +
            teams[0][index].forecastedXGConceded) /
            2
        )
      );
    }

    if (match.homeOdds === 0 && match.awayOdds === 0) {
      homeRaw = 0.0;
      awayRaw = 0.0;
    } else {
      homeRaw = (1 / match.homeOdds).toFixed(2);
      awayRaw = (1 / match.awayOdds).toFixed(2);
    }

    let formHome = teams[0][index];
    let formAway = teams[1][index];

    formHome.averageGoalDifferential =
      formHome.generalOffensiveRating - formAway.generalOffensiveRating;
    formHome.averageConcededDifferential =
      formHome.generalDefensiveRating - formAway.generalDefensiveRating;

    formAway.averageGoalDifferential =
      formAway.generalOffensiveRating - formHome.generalOffensiveRating;
    formAway.averageConcededDifferential =
      formAway.generalDefensiveRating - formHome.generalDefensiveRating;

    let oddsWeightingHome;
    let oddsWeightingAway;
    let homeWeighting;
    let awayWeighting;

    // console.log(match.game);

    // console.log("formHome.homeOffensiveRating")
    // console.log(formHome.homeOffensiveRating)
    // console.log("formAway.awayOffensiveRating")
    // console.log(formAway.awayOffensiveRating)

    // console.log("formHome.homeDefensiveRating")
    // console.log(formHome.homeDefensiveRating)
    // console.log("formAway.awayDefensiveRating")
    // console.log(formAway.awayDefensiveRating)

    let weightingSplitHome;
    let weightingSplitAway;
    let weighting;

    if (homeRaw > 0) {
      oddsWeightingHome = awayRaw - homeRaw ;
      oddsWeightingAway = homeRaw - awayRaw;

      weighting = (await diff(oddsWeightingHome, oddsWeightingAway));


      if (weighting >= 0) {
        weightingSplitHome = Math.abs(weighting) / 2;
        weightingSplitAway = -Math.abs(weighting) / 2;
      } else if (weighting < 0) {
        weightingSplitHome = -Math.abs(weighting) / 2;
        weightingSplitAway = Math.abs(weighting) / 2;
      } else {
        weightingSplitHome = 0;
        weightingSplitAway = 0;
      }
    } else {
      weightingSplitHome = 0;
      weightingSplitAway = 0;
    }


    homeWeighting = weightingSplitHome * 0.75
    awayWeighting = weightingSplitAway * 0.75


    console.log(match.game);
    console.log(`homeWeighting ${homeWeighting}`);
    console.log(`AWAY WEIGHTING ${awayWeighting}`);

    let homeCalculation;
    let awayCalculation;

    homeCalculation = parseFloat(homeWeighting);
    awayCalculation = parseFloat(awayWeighting);

    // let leagueName = allLeagueData.find(
    //   ({ competitionId }) => competitionId === match.competition_id
    // ).name;

    const homeGoalWeighting = formHome.homeAttackAdvantageWeighted / 75;
    const HomeDefenceWeighting = formHome.homeDefenceAdvantageWeighted / 75;

    const homeGoalsUnweighted = parseFloat(
      (formHome.XG + formHome.scoredAverage) / 2
    );

    const awayGoalsUnweighted = parseFloat(
      (formAway.XG + formAway.scoredAverage) / 2
    );

    const homeGoalsWithOddsWeighting = parseFloat(
      homeGoalsUnweighted + homeCalculation
    );

    const awayGoalsWithOddsWeighting = parseFloat(
      awayGoalsUnweighted + awayCalculation
    );

    const homeGoalsWithAwayDefenceWeighting = parseFloat(
      homeGoalsWithOddsWeighting * formAway.defenceRating
    );

    const awayGoalsWithHomeDefenceWeighting = parseFloat(
      awayGoalsWithOddsWeighting * formHome.defenceRating
    );

    let homeAdvantageAttackAdjustment = 1 + homeGoalWeighting / 3;
    let homeAdvantageDefenceAdjustment;

    if (HomeDefenceWeighting >= 0) {
      homeAdvantageDefenceAdjustment = 1 - HomeDefenceWeighting / 3;
    } else if (HomeDefenceWeighting < 0) {
      homeAdvantageDefenceAdjustment = 1 + HomeDefenceWeighting / 3;
    }

    let homeGoalswithHomeWeighting;
    let awayGoalswithAwayWeighting;

    homeGoalswithHomeWeighting = parseFloat(homeGoalsWithAwayDefenceWeighting);

    awayGoalswithAwayWeighting = parseFloat(awayGoalsWithHomeDefenceWeighting);

    if (homeGoalswithHomeWeighting < 0) {
      homeGoalswithHomeWeighting = 0;
    }

    if (awayGoalswithAwayWeighting < 0) {
      awayGoalswithAwayWeighting = 0;
    }

    let finalHomeGoals;
    let finalAwayGoals;

    async function roundCustom(num, form) {
      let wholeNumber = Math.floor(num);
      let remainder = num - wholeNumber;
      // if(form.lastGame === "W"){
      //   return Math.ceil(num)
      // } else if (form.lastGame === "L"){
      //   return Math.floor(num)
      // } else {
      //   return Math.round(num)
      // }
      if (form.finalFinishingScore <= 0.985) {
        return Math.floor(num);
      } else if (form.finalFinishingScore >= 1.015) {
        return Math.ceil(num);
      } else if (remainder < 0.85) {
        return Math.floor(num);
      } else if (remainder >= 0.85) {
        return Math.ceil(num);
      }
    }

    const XGAgainstAdjustedHomeGoals =
      homeGoalswithHomeWeighting * formAway.XGConcededAdjustment;
    const XGAgainstAdjustedAwayGoals =
      awayGoalswithAwayWeighting * formHome.XGConcededAdjustment;

    const XGAdjustedHomeGoals =
      (homeGoalswithHomeWeighting + XGAgainstAdjustedHomeGoals) / 2;
    const XGAdjustedAwayGoals =
      (awayGoalswithAwayWeighting + XGAgainstAdjustedAwayGoals) / 2;

    let gameHomeAttackAdvantage =
      formHome.averageGoalDifferential - formAway.averageGoalDifferential;

    let gameHomeDefenceAdvantage =
      formAway.averageConcededDifferential -
      formHome.averageConcededDifferential;

    let goalsDifferential = gameHomeAttackAdvantage + gameHomeDefenceAdvantage;

    let homeAttackDiff = parseFloat(await diff(gameHomeAttackAdvantage, 0));
    let homeDefenceDiff = parseFloat(await diff(gameHomeDefenceAdvantage, 0));

    const generalformHomeGoals =
      (formHome.generalOffensiveRating +
        homeCalculation +
        formAway.defenceRating * formAway.generalDefensiveRating +
        1 +
        homeGoalWeighting) /
      2;

    const generalformAwayGoals =
      (formAway.generalOffensiveRating +
        awayCalculation +
        formHome.defenceRating * formHome.generalDefensiveRating +
        1 -
        HomeDefenceWeighting) /
      2;

    let calculatedDifferential = generalformHomeGoals - generalformAwayGoals;

    console.log(match.game)
    console.log(`goalsDifferential ${goalsDifferential}`)
    console.log(`calculatedDifferential ${calculatedDifferential}`)

    let differentialWeighting = await diff(
      goalsDifferential,
      calculatedDifferential
    );

    console.log(`differentialWeighting ${differentialWeighting}`)

    let homeGoalDifferentialWeighting;
    let awayGoalDifferentialWeighting;

    if (differentialWeighting >= 0) {
      console.log("OVER")
      homeGoalDifferentialWeighting = differentialWeighting / 1.8;
      awayGoalDifferentialWeighting = -Math.abs(differentialWeighting) / 1.8;
    } else if (differentialWeighting < 0) {
      console.log("UNDER")
      homeGoalDifferentialWeighting = -Math.abs(differentialWeighting) / 1.8;
      awayGoalDifferentialWeighting = Math.abs(differentialWeighting) / 1.8;
    }

    console.log(`homeGoalDifferentialWeighting: ${homeGoalDifferentialWeighting}`)
    console.log(`awayGoalDifferentialWeighting: ${awayGoalDifferentialWeighting}`)

    // console.log("homeGoalDifferentialWeighting");
    // console.log(homeGoalDifferentialWeighting);
    // console.log("awayGoalDifferentialWeighting");
    // console.log(awayGoalDifferentialWeighting);

    let experimentalHomeGoals =
      (formHome.scoredAverage + formHome.XG) / 2 +
      homeGoalDifferentialWeighting;
    let experimentalAwayGoals =
      (formAway.scoredAverage + formAway.XG) / 2 +
      awayGoalDifferentialWeighting;

    const homeformHomeGoals =
      (formHome.homeOffensiveRating + homeCalculation) *
      formAway.XGConcededAdjustment *
      (1 + homeGoalWeighting);
    const awayformAwayGoals =
      (formAway.awayOffensiveRating + awayCalculation) *
      formHome.XGConcededAdjustment *
      (1 - HomeDefenceWeighting);

    let rawFinalHomeGoals = parseFloat(
      ((experimentalHomeGoals * 25 +
        formHome.goalsBasedOnAverages * 0 +
        XGAdjustedHomeGoals * 0) /
        25) *
        homeAdvantageAttackAdjustment
    );

    let rawFinalAwayGoals = parseFloat(
      ((experimentalAwayGoals * 25 +
        formAway.goalsBasedOnAverages * 0 +
        XGAdjustedAwayGoals * 0) /
        25) *
        homeAdvantageDefenceAdjustment
    );

    if (rawFinalAwayGoals < 0) {
      // console.log(`${match.homeTeam} before ${rawFinalHomeGoals}`)
      let difference = parseFloat(await diff(0, rawFinalAwayGoals));
      rawFinalHomeGoals = rawFinalHomeGoals + difference;
      // console.log(`${match.homeTeam} after ${rawFinalHomeGoals}`)
    }

    if (rawFinalHomeGoals < 0) {
      // console.log(`${match.awayTeam} before ${rawFinalAwayGoals}`)
      let difference = parseFloat(await diff(0, rawFinalHomeGoals));
      rawFinalAwayGoals = rawFinalAwayGoals + difference;
      // console.log(`${match.awayTeam} after ${rawFinalAwayGoals}`)
    }

    console.log(`rawFinalHomeGoals ${rawFinalHomeGoals}`)
    console.log(`rawFinalAwayGoals ${rawFinalAwayGoals}`)


    finalHomeGoals = await roundCustom(rawFinalHomeGoals, formHome);

    finalAwayGoals = await roundCustom(rawFinalAwayGoals, formAway);

    if (finalHomeGoals > finalAwayGoals) {
      match.prediction = "homeWin";
    } else if (finalAwayGoals > finalHomeGoals) {
      match.prediction = "awayWin";
    } else if (finalHomeGoals === finalAwayGoals) {
      match.prediction = "draw";
    }

    switch (true) {
      case match.homeGoals > match.awayGoals:
        match.winner = match.homeTeam;
        match.outcome = "homeWin";
        break;
      case match.homeGoals === match.awayGoals:
        match.winner = "draw";
        match.outcome = "draw";

        break;
      case match.homeGoals < match.awayGoals:
        match.winner = match.awayTeam;
        match.outcome = "awayWin";
        break;
      default:
        break;
    }

    console.log(match.prediction);
    console.log(match.outcome);
    if (match.status === "complete") {
      if (match.prediction === match.outcome) {
        match.predictionOutcome = "Won";
      } else if (match.prediction !== match.outcome) {
        match.predictionOutcome = "Lost";
      }
    }

    console.log(match.predictionOutcome);

    let total = parseInt(finalHomeGoals + finalAwayGoals);
    totalGoals = totalGoals + total;

    numberOfGames = numberOfGames + 1;

    if (finalHomeGoals < 0) {
      finalHomeGoals = 0;
    }

    if (finalAwayGoals < 0) {
      finalAwayGoals = 0;
    }

    if (match.status === "suspended") {
      finalHomeGoals = "P";
      finalAwayGoals = "P";
    }
    return [
      finalHomeGoals,
      finalAwayGoals,
      rawFinalHomeGoals,
      rawFinalAwayGoals,
    ];
  } else {
    finalHomeGoals = "-";
    finalAwayGoals = "-";
    rawFinalHomeGoals = "-";
    rawFinalAwayGoals = "-";
  }

  return [finalHomeGoals, finalAwayGoals, rawFinalHomeGoals, rawFinalAwayGoals];
}

async function getSuccessMeasure(fixtures) {
  let sum = 0;
  let gameCount = 0;
  let profit;

  for (let i = 0; i < fixtures.length; i++) {
    if (fixtures[i].status === "complete") {
      if (fixtures[i].predictionOutcome === "Won") {
        sum = sum + fixtures[i].profit;

        profit = sum.toFixed(2);

        gameCount = gameCount + 1;
      } else if (fixtures[i].predictionOutcome === "Lost") {
        sum = sum - 1;
        profit = sum.toFixed(2);
        gameCount = gameCount + 1;
      } else {
        sum = sum + 0;
        profit = sum.toFixed(2);
        gameCount = gameCount + 0;
      }
    }
  }

  if (gameCount > 0) {
    ReactDOM.render(
      <Fragment>
        <Div
          className={"SuccessMeasure"}
          text={`accumulated profit/loss if 1 was staked on each of the  ${gameCount} games: ${profit}`}
        />
      </Fragment>,
      document.getElementById("successMeasure")
    );
  } else {
    return;
  }
}

var tips = [];
var longShotTips = [];
var bttsArray = [];
var accumulatedOdds = 1;
let predictions = [];

export async function getScorePrediction(day) {
  let radioSelected = parseInt(selectedOption);
  tips = [];
  bttsArray = [];
  longShotTips = [];
  accumulatedOdds = 1;

  let index;
  let divider;
  if (radioSelected === 5) {
    index = 0;
    divider = 5;
  } else if (radioSelected === 6) {
    index = 1;
    divider = 6;
  } else if (radioSelected === 10) {
    index = 2;
    divider = 10;
  }

  await Promise.all(
    matches.map(async (match) => {
      // if there are no stored predictions, calculate them based on live data
      if (match) {
        switch (true) {
          case match.status === "!suspended":
            [
              match.goalsA,
              match.goalsB,
              match.unroundedGoalsA,
              match.unroundedGoalsB,
            ] = await calculateScore(match, index, divider, match.id);

            break;
          case match.status === "suspended" || match.status === "canceled":
            match.goalsA = "P";
            match.goalsB = "P";
            break;
          default:
            [
              match.goalsA,
              match.goalsB,
              match.unroundedGoalsA,
              match.unroundedGoalsB,
            ] = await calculateScore(match, index, divider, match.id);
            break;
        }
      } else {
        [
          match.goalsA,
          match.goalsB,
          match.unroundedGoalsA,
          match.unroundedGoalsB,
        ] = await calculateScore(match, index, divider, match.id);
      }

      let predictionObject;
      let longShotPredictionObject;

      if (
        match.unroundedGoalsA - incrementValue > match.unroundedGoalsB &&
        match.homeOdds !== 0 &&
        match.fractionHome !== "N/A"
      ) {
        accumulatedOdds =
          parseFloat(accumulatedOdds) * parseFloat(match.homeOdds);
        predictionObject = {
          team: match.homeTeam,
          odds: match.fractionHome,
          outcome: match.predictionOutcome,
          goalDifferential: parseFloat(
            await diff(match.unroundedGoalsA, match.unroundedGoalsB)
          ),
        };
        if (match.prediction !== "draw") {
          tips.push(predictionObject);
        }
      } else if (
        match.unroundedGoalsB - incrementValue > match.unroundedGoalsA &&
        match.awayOdds !== 0 &&
        match.fractionAway !== "N/A"
      ) {
        accumulatedOdds =
          parseFloat(accumulatedOdds) * parseFloat(match.awayOdds);
        predictionObject = {
          team: match.awayTeam,
          odds: match.fractionAway,
          outcome: match.predictionOutcome,
          goalDifferential: parseFloat(
            await diff(match.unroundedGoalsB, match.unroundedGoalsA)
          ),
        };
        if (match.prediction !== "draw") {
          tips.push(predictionObject);
        }
      }

      tips.sort(function (a, b) {
        return b.goalDifferential - a.goalDifferential;
      });

      await getBTTSPotential(allForm, match, index);
      if (
        match.btts === true &&
        match.status !== "suspended" &&
        match.status !== "canceled"
      ) {
        bttsArray.push(match);
      }

      if (
        match.unroundedGoalsA - 0.75 > match.unroundedGoalsB &&
        match.homeOdds >= 2.5
      ) {
        longShotPredictionObject = {
          team: match.homeTeam,
          odds: match.fractionHome,
          outcome: match.predictionOutcome,
          goalDifferential: parseFloat(
            await diff(match.unroundedGoalsA, match.unroundedGoalsB)
          ),
        };
        if (match.prediction !== "draw") {
          longShotTips.push(longShotPredictionObject);
        }
      } else if (
        match.unroundedGoalsA < match.unroundedGoalsB - 0.75 &&
        match.awayOdds >= 2.5
      ) {
        longShotPredictionObject = {
          team: match.awayTeam,
          odds: match.fractionAway,
          outcome: match.predictionOutcome,
          goalDifferential: parseFloat(
            await diff(match.unroundedGoalsB, match.unroundedGoalsA)
          ),
        };
        if (match.prediction !== "draw") {
          longShotTips.push(longShotPredictionObject);
        }
      }

      longShotTips.sort(function (a, b) {
        return b.goalDifferential - a.goalDifferential;
      });

      ReactDOM.render(
        <Fixture
          fixtures={matches}
          result={true}
          className={"individualFixture"}
        />,
        document.getElementById("FixtureContainer")
      );

      predictions.push(match);
    })
  );
  await getSuccessMeasure(matches);

  await renderTips();
}

async function renderTips() {
  ReactDOM.render(
    <div className="PredictionContainer">
      <Fragment>
        <Increment />
        <Collapsable
          buttonText={"Build a multi"}
          className={"PredictionsOfTheDay"}
          text={
            <ul className="BestPredictions">
              <div className="BestPredictionsExplainer">
                Change the size of the accumulator with the + and - buttons.
                Games with smaller predicted winning margins will be added or
                taken away with each tap. Multis are based on the form option
                selected, above
              </div>
              {tips.map((tip) => (
                <li className={tip.outcome} key={tip.team}>
                  {tip.team} odds: {tip.odds}
                </li>
              ))}
              <div className="AccumulatedOdds">{`Accumulator odds ~ : ${
                Math.round(accumulatedOdds) - 1
              }/1`}</div>
            </ul>
          }
        />
      </Fragment>
    </div>,
    document.getElementById("bestPredictions")
  );

  ReactDOM.render(
    <div>
      <Fragment>
        <Collapsable
          buttonText={"Longshot predictions"}
          text={
            <ul className="LongshotPredictions">
              <lh>To win</lh>
              {longShotTips.map((tip) => (
                <li className={tip.outcome} key={tip.team}>
                  {tip.team} odds: {tip.odds}
                </li>
              ))}
            </ul>
          }
        />
      </Fragment>
    </div>,
    document.getElementById("longShots")
  );

  ReactDOM.render(
    <div>
      <Fragment>
        <Collapsable
          className={"BTTSGames"}
          buttonText={"BTTS games"}
          text={
            <ul className="BTTSGames">
              <lh>Games with highest chance of BTTS</lh>
              {bttsArray.map((game) => (
                <li className={game.bttsOutcome} key={game.game}>
                  {game.game}
                </li>
              ))}
            </ul>
          }
        />
      </Fragment>
    </div>,
    document.getElementById("BTTS")
  );
}
